.header-container{
display: flex;
justify-content: space-between;
align-items: center;
width: 100%;
position: fixed;
padding: 0px 40px;
top: 0px;
height: 60px;
background-color: #222221;
}

.logo-container{
    display: flex;
    align-items: center;
    gap: 30px;
    width: 100%;

}

.logo-img{
    height: 55px;
}

.logo-container h1{
    color: rgb(212, 212, 212);
    font-size: 1.5rem;
}

.icons{
    display: flex;
    gap: 15px;
    margin-left: 10%;
    width: 10%;
}

.share-icon{
    color:  rgb(212, 212, 212);
    height: 18px;
}

.instagram-icon{
    color:  rgb(212, 212, 212);
    height: 19px;
}


.hidden-instagram-icon{
    height: 0px;
    width: 0px;
}

