.cart {
  height: 7%;
  width: 100%;
  padding-bottom: 15px;
  bottom: 0px;
  border-radius: 15px 15px 0px 0px;
  position: fixed;
  background-color: rgba(230, 230, 230, 0.959);
  transition: height 0.4s;
  /* Adicionando transição para suavizar a mudança de altura */
}

.cart.clicked {
  height: 40%;
}

.cart.clicked h3 {
  display: none;
  /* Esconde o h3 quando o carrinho é clicado */
}

.cart h3 {
  font-size: 1.2rem;
}

.cart ul {
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.cart ul li {
  list-style: none;
  border: 1px solid rgb(182, 182, 182);
  display: flex;
  background-color: white;
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  padding: 10px 30px;
  margin: 0px 10px;

}

.item-description {
  display: flex;
  gap: 0px 10px;
}


.cart button {
  background-color: #2aadbe;
  border: none;
  color: white;
  border-radius: 3px;
}

.cart button:hover {
  background-color: #217c88;
}

.add,
.remove {
  height: 25px;
  width: 25px;
}

.buy{
  height: 35px;
  padding: 10px;
}